.full-screen {
    #register-navbar a {
        color: #fff;
    }
    .page-header .container {
        margin-top: 11%;
        position: relative;
        z-index: 3;
    }
    .demo-footer {
        bottom: 10px;
        left: 0;
        color: #fff;
        position: absolute;
        z-index: 3;
        width: 100%;
    }
    .demo-footer .fa-heart {
        color: #eb5e28;
    }
    .demo-card {
        border-radius: 8px;
        max-width: 350px;
        margin: 20px 0;
        min-height: 400px;
        padding: 30px;
    }

    &.login {
        .demo-card {
            background-color: #ff8f5e;
            color: #fff;

            label {
                margin-top: 15px;
            }
            .title {
                color: #b33c12;
                text-align: center;
            }
            .btn {
                margin-top: 30px;
            }
            .forgot {
                text-align: center;
            }
        }
    }
    &.register-page {
        .page-header {
            .container {
                margin-bottom: 50px;

                .info-horizontal .icon {
                    margin-top: 10px;
                }
            }
        }

        .demo-card,
        .card-register {
            background-color: $white-color;
            max-width: 360px;

            input {
                margin-bottom: 8px;
            }
            .btn {
                margin-top: 20px;
            }
            .division {
                float: none;
                margin: 0 auto;
                overflow: hidden;
                margin: 20px 0;
                position: relative;
                text-align: center;
                width: 100%;
                color: $font-color;
                .line {
                    border-top: 1px solid rgba(0, 0, 0, 0.2);
                    position: absolute;
                    top: 10px;
                    width: 40%;
                    &.l {
                        left: 0;
                    }
                    &.r {
                        right: 0;
                    }
                }
            }
            .register-form {
                .form-control {
                    border: 1px solid #ccc5b9 !important;
                }
            }
            .login p {
                color: $font-color;
                a {
                    color: $info-color;
                }
            }
        }
        .social {
            margin-bottom: 10px;
            margin-top: 15px;
            text-align: center;
        }
        .login {
            margin-top: 20px;
            text-align: center;
            p {
                font-size: 0.9em;
            }
        }
    }
}
