.section {
    padding: 70px 0;
    position: relative;
    background-color: #ffffff;
}
.section-with-space {
    padding: 60px 0;
}
.section-gray {
    background-color: #eeeeee;
}
.section-nude {
    background-color: $bg-nude;
}
.section-gold {
    background-color: #caac90;
    color: $white-color;
}
.section-brown {
    background-color: #a59e94;
    color: $white-color;
}
.section-light-blue {
    background-color: $primary-color;
    color: $white-color;
}
.section-dark-blue {
    background-color: #506367;
    color: $white-color;
}
.section-dark {
    background-color: #151f20;
}
.section-image,
.section-gold,
.section-dark {
    position: relative;

    .title,
    .info-title,
    .card-plain .card-title {
        color: $white-color !important;
    }
    .category,
    .description,
    .card-plain .card-description,
    .card-plain .card-category {
        color: rgba($white-color, 0.7);
    }
    hr {
        border-color: rgba(255, 255, 255, 0.19);
    }
}
.page-header,
.carousel-caption {
    .title,
    .info-title {
        color: $white-color;
    }
}
[class*="features-"],
[class*="team-"],
[class*="projects-"],
[class*="pricing-"],
[class*="testimonials-"],
[class*="contactus-"] {
    padding: 80px 0;
}
.section-image {
    background-position: center center;
    background-size: cover;

    &:before {
        background-color: rgba(0, 0, 0, 0.5);
        content: "";
        display: block;
        height: 100%;
        left: 0;
        top: 0;
        position: absolute;
        width: 100%;
        z-index: 1;
    }
    & .container {
        position: relative;
        z-index: 2;
    }
}

// Sections
@import "sections/headers";
@import "sections/features";
@import "sections/blogs";
@import "sections/teams";
@import "sections/projects";
@import "sections/pricing";
@import "sections/testimonials";
@import "sections/contact-us";
